.award-article {
	background-color: #FFF;
	hr {
		border: none;
		background-color: #CCC;
		width: 150px;
		height: 1px;
		margin: 24px auto;
	}
	article {
		padding-top: 64px;
		width: $award_article_width;
		margin-left: auto;
		margin-right: auto;
		.article-header {
			text-align: center;
			hr {
				margin: 12px auto;
			}
			.groupname {
				font-size: 1.3rem;
				margin-top: 24px;
			}
			mark {
				display: block;
				text-align: center;
				background: none;
				font-size: 1.4rem;
				line-height: 1;
				font-weight: 300;
				margin-bottom: 8px;
			}
			h1 {
				font-size: 2.8rem;
				font-weight: 600;
				line-height: 1.6;
			}
			.meta {
				margin-top: 24px;
				font-size: 1.3rem;
				.category {
					a {
						color: $black;
						@include animation(.2s,ease-in);
						text-decoration: underline;
						&:hover {
							color: #CCC;
							@include animation(.2s,ease-in);
						}
					}
				}
				.awards {
					margin-bottom: 24px;
					letter-spacing: 0.6px;				
					ul {
						li {
							display: inline-block;
							line-height: 30px;
							border-radius: 30px;
							background-color: #fbfbfb;
							overflow: hidden;
							font-size: 1.4rem;
							font-weight: 300;
							padding-left: 20px;
							padding-right: 20px;
							margin: 0 5px;
							&:before {
								font-family: "Ionicons";
								content: "\f356";
								color: $red;
								display: inline-block;
								margin-right: 10px;
							}
						}
					}
				}
			}
			.description {
				margin-top: 64px;
				font-size: 1.6rem;
				line-height: 1.8;
				font-weight: 400;
				color: #696969;
				text-align: left;
			}
			.socials {
				margin-top: 48px;
				height: 18px;
				@include flex(nowrap,center,center);
				a {
					border: none;
					display: block;
					margin: 0 16px;
					line-height: 1;
					color: $black;
					&:first-of-type {
						margin-left: 0;
					}
					&:last-of-type {
						margin-right: 0;
					}
					.blogicon-bookmark {
						font-size: 3.6rem;
						position: relative;
						top: -3px;
					}
					.ion-social-twitter,
					.ion-social-facebook,
					.ion-social-googleplus,
					.fa-get-pocket {
						font-size: 2.8rem;
					}
				}
			}
		}
		.entry-body {
			margin-top: 48px;
			font-size: 1.6rem;
			line-height: 1.8;
			font-weight: 400;
			color: #696969;
			figure,
			blockquote,
			p {
				display: block;
				margin-bottom: 48px;
			}
			figure {
				text-align: center;
				.img {
					overflow: hidden;
					img {
						max-width: 100%;
						display: block;
						margin-left: auto;
						margin-right: auto;
					}
				}
				figcaption {
					margin-top: 12px;
					font-size: 1.4rem;
				}
			}
			h2 {
				font-size: 2.4rem;
				color: $black;
				font-weight: 600;
				margin-bottom: 24px;
			}
			blockquote {
				font-style: italic;
				padding-left: 24px;
				padding-right: 24px;
			}
		}
		#credit {
			text-align: center;
			font-size: 1.3rem;
			font-style: italic;
			a {
				color: $black;
				@include animation(.2s,ease-in);
				text-decoration: underline;
				vertical-align: middle;
				&:hover {
					color: #CCC;
					@include animation(.2s,ease-in);
				}
				i {
					display: inline-block;
					margin-left: 10px;
					font-size: 1.9rem;
					line-height: 1;
					position: relative;
					top: 3px;
				}
				span {
					line-height: 1;
					display: inline-block;
				}
			}
			
		}
		.entry-footer {
			margin-top: 64px;
			padding-bottom: 64px;
			position: relative;
			.meta {
				text-align: center;
				margin-top: 24px;
				.category {
					a {
						font-size: 1.3rem;
						color: $black;
						@include animation(.2s,ease-in);
						text-decoration: underline;
						&:hover {
							color: #CCC;
							@include animation(.2s,ease-in);
						}
					}
				}
				.awards {
					margin-bottom: 24px;
					letter-spacing: 0.6px;				
					ul {
						li {
							display: inline-block;
							line-height: 30px;
							border-radius: 30px;
							background-color: #fbfbfb;
							overflow: hidden;
							font-size: 1.4rem;
							font-weight: 300;
							padding-left: 20px;
							padding-right: 20px;
							&:before {
								font-family: "Ionicons";
								content: "\f356";
								color: $red;
								display: inline-block;
								margin-right: 10px;
							}
						}
					}
				}
			}
			.socials {
				margin-top: 48px;
				height: 18px;
				@include flex(nowrap,center,center);
				a {
					border: none;
					display: block;
					margin: 0 16px;
					line-height: 1;
					color: $black;
					&:first-of-type {
						margin-left: 0;
					}
					&:last-of-type {
						margin-right: 0;
					}
					.blogicon-bookmark {
						font-size: 3.6rem;
						position: relative;
						top: -3px;
					}
					.ion-social-twitter,
					.ion-social-facebook,
					.ion-social-googleplus,
					.fa-get-pocket {
						font-size: 2.8rem;
					}
				}
			}
		}
	}
	#relative-article {
		background-color: #fbfbfb;
		box-sizing: border-box;
		padding: 64px 0;
		text-align: center;
		hr {
			margin: 24px auto 48px auto;
		}
		h3 {
			font-size: 1.6rem;
			font-weight: 500;
			line-height: 1;
		}
		.carousel {
			margin-left: auto;
			margin-right: auto;
			width: $pc_width;
			position: relative;
			#relative-article-container {
				.articles {
					@include flex(nowrap,center,stretch);
					.article {
						text-align: left;
						box-sizing: border-box;
						box-shadow: 0 0 5px rgba(190,190,190,0.5);
						margin: 0 6px;
						width: 378px;
						position: relative;
						@include animation(.2s,ease-in);
						mark {
							position: absolute;
							top: 0;
							left: 0;
							z-index: 2;
							background: none;
							@include flex(wrap,flex-start,center);
							background-color: $red;
							color: #FFF;
							padding: 4px 12px;
							i {
								font-size: 1.7rem;
								margin-right: 5px;						
							}
							span {
								font-size: 1.2rem;
								letter-spacing: 1px;
							}
						}
						> ul,
						> hr {
							display: none;
						}
						a {
							background-color: #FFF;
							padding: 10px;
							color: $black;
							display: block;
							@include animation(.3s,ease-in);
							&:hover {
								@include animation(.3s,ease-in);
								background-color: $red;
								opacity: 1;
								color: #FFF;
								.story-category {
									color: #FFF;
								}
							}
							figure {
								position: relative;
								.img {
									position: relative;
									@include background(center,center);
									height: 160px;
									ul {
										position: absolute;
										bottom: 5px;
										left: 5px;
										padding: 6px 8px;
										background-color: rgba(255,255,255,0.9);
										border-radius: 3px;
										li {
											font-weight: 300;
											font-size: 1rem;
											color: $black;
											margin-bottom: 5px;
											&:last-child {
												margin-bottom: 0;
											}
											&:before {
												color: $red;
												font-family: "Ionicons";
												content: "\f356";
												display: inline-block;
												margin-right: 5px;
											}
										}
									}
								}
								figcaption {
									margin-top: 10px;
									padding: 5px 5px 40px;
									box-sizing: border-box;
									height: 100%;											
									strong {
										font-size: 1.3rem;
										font-weight: 300;
										display: block;	
									}
									.groupname {
										font-size: 1.2rem;
										font-weight: 300;
										margin-top: 10px;
										line-height: 1;
									}
									.story-category-sp {
										display: none;
									}
								}
							}
							.story-category {					
								position: absolute;
								bottom: 15px;
								left: 15px;
								width: 100%;
								font-size: 1.1rem;
								color: $red;
								display: inline-block;
								line-height: 1;
							}
						}			
					}				
				}					
			}			
		}
	}
}

@media only screen and (max-width: 1120px) {
	.award-article {
		article {
			width: auto;	
			padding-left: 24px;
			padding-right: 24px;
			.article-header {
				
			}
			.entry-body {
				margin-bottom: 32px;
				width: auto;
				box-sizing: border-box;
				max-width: auto;
			}		
		}
		#relative-article {
			padding-left: 24px;
			padding-right: 24px;
			h3 {
				font-size: 1.4rem;
			}
			.carousel {
				width: 1024px;
				box-sizing: border-box;
				#relative-article-container {
					.articles {
						.article {
							width: 313.3333333333333px;
						}							
					}		
				}	
			}
		}	
	}
}

@media screen and (max-width: 768px) {
	.award-article {
		hr {
			width: 50px;
		}
		article {
			width: auto;
			margin-left: 0;
			margin-right: 0;
			padding-top: 32px;
			padding-bottom: 32px;
			.article-header {
				h1 {
					font-size: 2rem;
				}
				.description {
					margin-top: 32px;
					font-size: 1.3rem;
				}
				.meta {
					.awards {
						margin-bottom: 24px;
						letter-spacing: 0.6px;				
						ul {
							li {
								display: inline-block;
								line-height: 24px;
								border-radius: 24px;
								font-size: 1.2rem;
								padding-left: 10px;
								padding-right: 10px;
								margin-bottom: 12px;
								&:last-child {
									margin-bottom: 0;
								}
								&:before {
									font-family: "Ionicons";
									content: "\f356";
									color: $red;
									display: inline-block;
									margin-right: 5px;
								}
							}
						}
					}
				}
				.socials {
					margin-top: 32px;
					height: 14px;
					a {
						margin-right: 8px;
						.blogicon-bookmark {
							font-size: 3.1rem;
						}
						.ion-social-twitter,
						.ion-social-facebook,
						.ion-social-googleplus,
						.fa-get-pocket {
							font-size: 2.4rem;
						}
					}
				}
			}
			.entry-body {
				margin-top: 48px;
				font-size: 1.4rem;
				line-height: 1.8;
				figure,
				h2,
				blockquote,
				p {
					margin-bottom: 24px;
				}
				figure {
					figcaption {
						margin-top: 6px;
						font-size: 1.2rem;
					}
				}
				h2 {
					font-size: 1.8rem;
				}
				blockquote {
					padding: 0 16px;
				}
			}
			.entry-footer {
				margin-top: 32px;
				.meta {
					.awards {
						margin-bottom: 24px;
						letter-spacing: 0.6px;				
						ul {
							li {
								display: inline-block;
								line-height: 24px;
								border-radius: 24px;
								font-size: 1.2rem;
								padding-left: 10px;
								padding-right: 10px;
								margin-bottom: 12px;
								&:last-child {
									margin-bottom: 0;
								}
								&:before {
									font-family: "Ionicons";
									content: "\f356";
									color: $red;
									display: inline-block;
									margin-right: 5px;
								}
							}
						}
					}
				}
				.socials {
					margin-top: 32px;
					height: 14px;
					a {
						.blogicon-bookmark {
							font-size: 3.1rem;
						}
						.ion-social-twitter,
						.ion-social-facebook,
						.ion-social-googleplus,
						.fa-get-pocket {
							font-size: 2.4rem;
						}
					}
				}
			}
		}
		#relative-article {
			padding-top: 32px;
			padding-bottom: 32px;
			padding-left: 12px;
			padding-right: 12px;
			hr {
				width: 50px;
			}
			h3 {
				font-size: 1.3rem;
			}
			.carousel {
				margin-left: 0;
				margin-right: 0;
				width: 100%;
				#relative-article-container {					
					.articles {
						display: block;
						.article {
							margin: 0 0 10px 0;
							width: 100%;
							box-sizing: border-box;
							padding: 10px;
							background-color: #FFF;
							mark {
								padding: 3px 10px;
								i {
									font-size: 1.4rem;						
								}
								span {
									font-size: 1rem;
								}
							}
							ul {
								margin-top: 6px;
								display: block;
								li {
									text-align: center;
									font-weight: 300;
									font-size: 0.9rem;
									color: $black;
									margin-bottom: 5px;
									&:last-child {
										margin-bottom: 0;
									}
									&:before {
										color: $red;
										font-family: "Ionicons";
										content: "\f356";
										display: inline-block;
										margin-right: 5px;
									}
								}
							}
							> hr {
								display: block;
								width: 80px;
								border: none;
								margin: 10px auto 15px auto;
								height: 1px;
								background-color: #CCC;
							}
							a {
								padding: 0;
								figure {
									@include flex(nowrap,space-between,stretch);
									.img {
										width: 120px;
										min-height: 90px;
										@include background(center,center);
										height: auto;
										ul {
											display: none;
										}
									}
									figcaption {
										position: relative;
										flex: 1;
										margin-top: 0;
										min-height: 90px;
										padding: 0 0 0 10px;									
										strong {
											font-size: 1.2rem;
										}
										.groupname {
											font-size: 1.1rem;
										}
										.story-category-sp {
											position: absolute;
											bottom: 0;
											left: 10px;
											width: 100%;
											display: block;
											color: $red;
											font-size: 1rem;
											line-height: 1;
										}
									}
								}
								.story-category {
									display: none;
								}
								&:hover {
									background-color: #FFF;
									color: $black;
									figure {
										figcaption {
											.story-category-sp {
												color: $red;
											}
										}
									}
								}						
							}
						}
					}		
				}	
			}
		}
	}
}