html {
	overflow-x:hidden;
	overflow-y:auto;
	font-size: 62.5%;
}

body {
	font-family: "Helvetica","Noto Sans JP","Yu Gothic",YuGothic,"ヒラギノ角ゴシック Pro","Hiragino Kaku Gothic Pro","メイリオ",Meiryo,Osaka,"ＭＳ Ｐゴシック","MS PGothic",sans-serif;
	font-size: 10px;
	font-size: 1.0rem; 
	line-height: 1.5;
	position: relative;
	color: $black;
	padding: 0;
	margin: 0;
	background-color: #FFF;
	//min-width: $pc_width;
	width: 100%;
	height: 100%;
	letter-spacing: normal;
	-webkit-font-smoothing: none; //ぎざぎざ
    -webkit-font-smoothing: antialiased; //なめらか
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale; //なめらか
    -moz-osx-font-smoothing: unset; //デフォルトに戻す
    -moz-osx-font-smoothing: inherit; //親要素から継承
    -moz-osx-font-smoothing: auto; //システムのデフォルトに応じる
    &.award-list {
	    background-color: $black;
    }
	&.locked {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.fb-quote {
	display: none;
}

::selection {
	background: rgba($red, 0.5);
	color: #FFF;
}

::-moz-selection {
	background: rgba($red, 0.5);
	color: #FFF;
}

a {
	text-decoration: none;
	color: $red;
	@include animation(.2s,ease-in);
	opacity: 1;
	&:hover {
		@include animation(.2s,ease-in);
		opacity: 0.8;
	}
	img,
	svg {
		border: none;
		vertical-align: bottom;
	}
}

.st0{
	fill:#FF2500;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong,
b {
	font-weight: bold;
}

@media only screen and (max-width: 1120px) {
	body {
		min-width: auto;
	}
}



